//progressive
ul#progressive-bar{
  max-width: 500px;
  min-width: 350px;
  position: relative;
  text-align: center;
  margin-bottom: 20px;
  overflow: hidden;
  counter-reset: step;
  @extend #center-block;

  li{
    list-style-type: none;
    float: left;
    position: relative;
    width: 30%;
    font-size: 9px;
    &:before{
      content: counter(step);
      counter-increment: step;
      width: 20px;
      line-height: 20px;
      display: block;
      font-size: 10px;
      color: #333;
      background: white;
      border-radius: 3px;
      margin: 0 auto 5px auto;
      border: 1px solid $yellow;
    }
    &:after{
      content: '';
      width: 100%;
      height: 2px;
      background: $yellow;
      position: absolute;
      left: -50%;
      top: 9px;
      z-index: -1; /*put it behind the numbers*/
    }
    //connector not need before first
    &:first-child:after{
      content: none;
    }

  }
  li.active:before,li.active:after{
    background: $yellow;
    color: white;
  }

}
