//view player
section#viewPlayer{
  @extend #container;
  aside{
    div#player_image{
      img{
        max-width: 200px;
        margin-bottom: 20px;
      }
    }

    div#player_info{
      h4{
        color: $blue;
      }
    }
  }

}

@media screen and (max-width: 350px) {
  header#tagline{
    h1{
      font-size: 28px !important;
    }
  }
  //event preview
  aside#event-preview{
    div#preview{
      margin: 0!important;
      height: 180px !important;
      img{
        width: 200px !important;
      }
    }
  }

  //access
  div#access{
    form{
      div.input-group{
        width: 105px !important;
      }
    }
    div.dropdown{
      button#filterDrop{
        width: 60px !important;
      }
    }
  }
  //end access
  //footer
  div#footer{
    footer{
      aside#copyright ul li{
        font-size: 8px;
      }

    }
  }
  //banners
  div#home-banner{
    div#banners{
      .swiper-slide{
        height: 180px !important;
      }
    }
  }
}
@media screen and (max-width: 599px) {
  aside#event-preview{
    div#preview{
      margin: 0 -20px 5px 0% !important;

      height: 115px !important;
      img{
        width: 160px !important;
      }
    }
  }
  //banners
  div#home-banner{
    div#banners{
      .swiper-slide{
        height: 200px !important;
      }
    }
  }
 
  //access
  div#access{

    div.dropdown{
      button#filterDrop{
        width: 70px !important;
      }
    }
  }
  div#footer{
    footer{
      aside#copyright ul li{
        font-size: 10px;
      }
      ul#footer-menu{
        font-size: 15px;
      }

    }
  }
}
@media screen and (max-width: 767px){
  //access
  aside#event-preview{
    div#preview{
      height: 115px !important;
      img{
        width: 160px !important;
      }
    }
  }

  //banners
  div#home-banner{
    div#banners{
      .swiper-slide{
        height: 240px !important;
      }
    }
  }

}
