ul#progressive-bar {
  max-width: 500px;
  min-width: 350px;
  position: relative;
  text-align: center;
  margin-bottom: 20px;
  overflow: hidden;
  counter-reset: step;
}

ul#progressive-bar li {
  list-style-type: none;
  float: left;
  position: relative;
  width: 30%;
  font-size: 9px;
}

ul#progressive-bar li:before {
  content: counter(step);
  counter-increment: step;
  width: 20px;
  line-height: 20px;
  display: block;
  font-size: 10px;
  color: #333;
  background: white;
  border-radius: 3px;
  margin: 0 auto 5px auto;
  border: 1px solid #E8B617;
}

ul#progressive-bar li:after {
  content: '';
  width: 100%;
  height: 2px;
  background: #E8B617;
  position: absolute;
  left: -50%;
  top: 9px;
  z-index: -1;
  /*put it behind the numbers*/
}

ul#progressive-bar li:first-child:after {
  content: none;
}

ul#progressive-bar li.active:before, ul#progressive-bar li.active:after {
  background: #E8B617;
  color: white;
}

